	import {
		createRouter,
		createWebHashHistory
	} from 'vue-router'
	import xinxicaijiyuan from '@/views/xinxicaijiyuan/list'
	import shebeileixing from '@/views/shebeileixing/list'
	import yihurenyuan from '@/views/yihurenyuan/list'
	import weixiuyuan from '@/views/weixiuyuan/list'
	import weixiushenqing from '@/views/weixiushenqing/list'
	import cangkuguanliyuan from '@/views/cangkuguanliyuan/list'
	import shenqingshebei from '@/views/shenqingshebei/list'
	import shebeiruku from '@/views/shebeiruku/list'
	import shebeixinxi from '@/views/shebeixinxi/list'
	import weixiuxinxi from '@/views/weixiuxinxi/list'
	import gonggaoxinxi from '@/views/gonggaoxinxi/list'
	import shebeichuku from '@/views/shebeichuku/list'
	import xinxicaijiyuanRegister from '@/views/xinxicaijiyuan/register'
	import xinxicaijiyuanCenter from '@/views/xinxicaijiyuan/center'
	import weixiuyuanRegister from '@/views/weixiuyuan/register'
	import weixiuyuanCenter from '@/views/weixiuyuan/center'
	import cangkuguanliyuanRegister from '@/views/cangkuguanliyuan/register'
	import cangkuguanliyuanCenter from '@/views/cangkuguanliyuan/center'
	import yihurenyuanRegister from '@/views/yihurenyuan/register'
	import yihurenyuanCenter from '@/views/yihurenyuan/center'

export const routes = [{
		path: '/login',
		name: 'login',
		component: () => import('../views/login.vue')
	},{
		path: '/',
		name: '首页',
		component: () => import('../views/index'),
		children: [{
			path: '/',
			name: '首页',
			component: () => import('../views/HomeView.vue'),
			meta: {
				affix: true
			}
		}, {
			path: '/updatepassword',
			name: '修改密码',
			component: () => import('../views/updatepassword.vue')
		}
		
		,{
			path: '/xinxicaijiyuanCenter',
			name: '信息采集员个人中心',
			component: xinxicaijiyuanCenter
		}
		,{
			path: '/weixiuyuanCenter',
			name: '维修员个人中心',
			component: weixiuyuanCenter
		}
		,{
			path: '/cangkuguanliyuanCenter',
			name: '仓库管理员个人中心',
			component: cangkuguanliyuanCenter
		}
		,{
			path: '/yihurenyuanCenter',
			name: '医护人员个人中心',
			component: yihurenyuanCenter
		}
		,{
			path: '/xinxicaijiyuan',
			name: '信息采集员',
			component: xinxicaijiyuan
		}
		,{
			path: '/shebeileixing',
			name: '设备类型',
			component: shebeileixing
		}
		,{
			path: '/yihurenyuan',
			name: '医护人员',
			component: yihurenyuan
		}
		,{
			path: '/weixiuyuan',
			name: '维修员',
			component: weixiuyuan
		}
		,{
			path: '/weixiushenqing',
			name: '维修申请',
			component: weixiushenqing
		}
		,{
			path: '/cangkuguanliyuan',
			name: '仓库管理员',
			component: cangkuguanliyuan
		}
		,{
			path: '/shenqingshebei',
			name: '申请设备',
			component: shenqingshebei
		}
		,{
			path: '/shebeiruku',
			name: '设备入库',
			component: shebeiruku
		}
		,{
			path: '/shebeixinxi',
			name: '设备信息',
			component: shebeixinxi
		}
		,{
			path: '/weixiuxinxi',
			name: '维修信息',
			component: weixiuxinxi
		}
		,{
			path: '/gonggaoxinxi',
			name: '公告信息',
			component: gonggaoxinxi
		}
		,{
			path: '/shebeichuku',
			name: '设备出库',
			component: shebeichuku
		}
		]
	},
	{
		path: '/xinxicaijiyuanRegister',
		name: '信息采集员注册',
		component: xinxicaijiyuanRegister
	},
	{
		path: '/weixiuyuanRegister',
		name: '维修员注册',
		component: weixiuyuanRegister
	},
	{
		path: '/cangkuguanliyuanRegister',
		name: '仓库管理员注册',
		component: cangkuguanliyuanRegister
	},
	{
		path: '/yihurenyuanRegister',
		name: '医护人员注册',
		component: yihurenyuanRegister
	},
]

const router = createRouter({
	history: createWebHashHistory(process.env.BASE_URL),
	routes
})

export default router
