<template>
	<div>
		<el-dialog v-model="formVisible" :title="formTitle" width="80%" destroy-on-close :fullscreen='false'>
			<el-form class="formModel_form" ref="formRef" :model="form" label-width="$template2.back.add.form.base.labelWidth" :rules="rules">
				<el-row>
					<el-col :span="12">
						<el-form-item label="设备编号" prop="shebeibianhao">
							<el-select
								class="list_sel"
								:disabled="!isAdd||disabledForm.shebeibianhao?true:false"
								v-model="form.shebeibianhao" 
								placeholder="请选择设备编号"
								@change="shebeibianhaoChange">
								<el-option v-for="(item,index) in shebeibianhaoLists" :label="item" :value="item">
								</el-option>
							</el-select>
						</el-form-item>
					</el-col>
					<el-col :span="12">
						<el-form-item label="设备名称" prop="shebeimingcheng">
							<el-input class="list_inp" v-model="form.shebeimingcheng" placeholder="设备名称"
								 type="text" 								:readonly="!isAdd||disabledForm.shebeimingcheng?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="12">
						<el-form-item label="设备型号" prop="shebeixinghao">
							<el-input class="list_inp" v-model="form.shebeixinghao" placeholder="设备型号"
								 type="text" 								:readonly="!isAdd||disabledForm.shebeixinghao?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="12">
						<el-form-item label="设备数量" prop="shebeishuliang">
							<el-input class="list_inp" v-model.number="form.shebeishuliang" placeholder="设备数量"
								 type="text" 								:readonly="!isAdd||disabledForm.shebeishuliang?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="12">
						<el-form-item label="设备位置" prop="shebeiweizhi">
							<el-input class="list_inp" v-model="form.shebeiweizhi" placeholder="设备位置"
								 type="text" 								:readonly="!isAdd||disabledForm.shebeiweizhi?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="12">
						<el-form-item label="设备类型" prop="shebeileixing">
							<el-input class="list_inp" v-model="form.shebeileixing" placeholder="设备类型"
								 type="text" 								:readonly="!isAdd||disabledForm.shebeileixing?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="12">
						<el-form-item label="报修时间" prop="baoxiushijian">
							<el-date-picker
								class="list_date"
								v-model="form.baoxiushijian"
								format="YYYY-MM-DD HH:mm:ss"
								value-format="YYYY-MM-DD HH:mm:ss"
								type="datetime"
								:readonly="!isAdd||disabledForm.baoxiushijian?true:false"
								placeholder="请选择报修时间" />
						</el-form-item>
					</el-col>
					<el-col :span="12">
						<el-form-item label="人员账号" prop="renyuanzhanghao">
							<el-input class="list_inp" v-model="form.renyuanzhanghao" placeholder="人员账号"
								 type="text" 								:readonly="!isAdd||disabledForm.renyuanzhanghao?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="12">
						<el-form-item label="人员姓名" prop="renyuanxingming">
							<el-input class="list_inp" v-model="form.renyuanxingming" placeholder="人员姓名"
								 type="text" 								:readonly="!isAdd||disabledForm.renyuanxingming?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="12">
						<el-form-item label="维修员账号" prop="weixiuyuanzhanghao">
							<el-select
								class="list_sel"
								:disabled="!isAdd||disabledForm.weixiuyuanzhanghao?true:false"
								v-model="form.weixiuyuanzhanghao" 
								placeholder="请选择维修员账号"
								@change="weixiuyuanzhanghaoChange">
								<el-option v-for="(item,index) in weixiuyuanzhanghaoLists" :label="item" :value="item">
								</el-option>
							</el-select>
						</el-form-item>
					</el-col>
					<el-col :span="12">
						<el-form-item label="维修员姓名" prop="weixiuyuanxingming">
							<el-input class="list_inp" v-model="form.weixiuyuanxingming" placeholder="维修员姓名"
								 type="text" 								:readonly="!isAdd||disabledForm.weixiuyuanxingming?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="12">
						<el-form-item label="报修内容" prop="baoxiuneirong">
							<el-input v-model="form.baoxiuneirong" placeholder="报修内容" type="textarea"
							:readonly="!isAdd||disabledForm.baoxiuneirong?true:false"
							/>
						</el-form-item>
					</el-col>
				</el-row>
			</el-form>
			<template #footer v-if="isAdd||type=='logistics'||type=='reply'">
				<span class="formModel_btn_box">
					<el-button class="formModel_cancel" @click="closeClick">取消</el-button>
					<el-button class="formModel_confirm" type="primary" @click="save"
						>
						提交
					</el-button>
				</span>
			</template>
		</el-dialog>
	</div>
</template>
<script setup>
	import {
		reactive,
		ref,
		getCurrentInstance,
		nextTick,
		computed,
		defineEmits
	} from 'vue'
	const context = getCurrentInstance()?.appContext.config.globalProperties;	
	const emit = defineEmits(['formModelChange'])
	//基础信息
	const tableName = 'weixiushenqing'
	const formName = '维修申请'
	//基础信息
	//form表单
	const form = ref({})
	const disabledForm = ref({
		shebeibianhao : false,
		shebeimingcheng : false,
		shebeixinghao : false,
		shebeishuliang : false,
		shebeiweizhi : false,
		shebeileixing : false,
		baoxiuneirong : false,
		baoxiushijian : false,
		renyuanzhanghao : false,
		renyuanxingming : false,
		weixiuyuanzhanghao : false,
		weixiuyuanxingming : false,
	})
	const formVisible = ref(false)
	const isAdd = ref(false)
	const formTitle = ref('')
	//表单验证
	//匹配整数
	const validateIntNumber = (rule, value, callback) => {
		if (!value) {
			callback();
		} else if (!context?.$toolUtil.isIntNumer(value)) {
			callback(new Error("请输入整数"));
		} else {
			callback();
		}
	}
	//匹配数字
	const validateNumber = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isNumber(value)) {
			callback(new Error("请输入数字"));
		} else {
			callback();
		}
	}
	//匹配手机号码
	const validateMobile = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isMobile(value)) {
			callback(new Error("请输入正确的手机号码"));
		} else {
			callback();
		}
	}
	//匹配电话号码
	const validatePhone = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isPhone(value)) {
			callback(new Error("请输入正确的电话号码"));
		} else {
			callback();
		}
	}
	//匹配邮箱
	const validateEmail = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isEmail(value)) {
			callback(new Error("请输入正确的邮箱地址"));
		} else {
			callback();
		}
	}
	//匹配身份证
	const validateIdCard = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.checkIdCard(value)) {
			callback(new Error("请输入正确的身份证号码"));
		} else {
			callback();
		}
	}
	//匹配网站地址
	const validateUrl = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isURL(value)) {
			callback(new Error("请输入正确的URL地址"));
		} else {
			callback();
		}
	}
	const rules = ref({
		shebeibianhao: [
		],
		shebeimingcheng: [
			{required: true,message: '请输入',trigger: 'blur'}, 
		],
		shebeixinghao: [
			{required: true,message: '请输入',trigger: 'blur'}, 
		],
		shebeishuliang: [
			{ validator: validateIntNumber, trigger: 'blur' },
		],
		shebeiweizhi: [
		],
		shebeileixing: [
		],
		baoxiuneirong: [
		],
		baoxiushijian: [
		],
		renyuanzhanghao: [
		],
		renyuanxingming: [
		],
		weixiuyuanzhanghao: [
		],
		weixiuyuanxingming: [
		],
	})
	//表单验证
	
	const formRef = ref(null)
	const id = ref(0)
	const type = ref('')
	//设备编号列表
	const shebeibianhaoLists = ref([])
	//维修员账号列表
	const weixiuyuanzhanghaoLists = ref([])
	//methods

	//获取唯一标识
	const getUUID =()=> {
      return new Date().getTime();
    }
	//重置
	const resetForm = () => {
		form.value = {
			shebeibianhao: '',
			shebeimingcheng: '',
			shebeixinghao: '',
			shebeishuliang: '',
			shebeiweizhi: '',
			shebeileixing: '',
			baoxiuneirong: '',
			baoxiushijian: '',
			renyuanzhanghao: '',
			renyuanxingming: '',
			weixiuyuanzhanghao: '',
			weixiuyuanxingming: '',
		}
	}
	//获取info
	const getInfo = ()=>{
		context?.$http({
			url: `${tableName}/info/${id.value}`,
			method: 'get'
		}).then(res => {
			let reg=new RegExp('../../../file','g')
			form.value = res.data.data
			formVisible.value = true
		})
	}
	const crossRow = ref('')
	const crossTable = ref('')
	const crossTips = ref('')
	const crossColumnName = ref('')
	const crossColumnValue = ref('')
	//初始化
	const init=(formId=null,formType='add',formNames='',row=null,table=null,statusColumnName=null,tips=null,statusColumnValue=null)=>{
		resetForm()
			form.value.baoxiushijian = context?.$toolUtil.getCurDateTime()
		if(formId){
			id.value = formId
			type.value = formType
		}
		if(formType == 'add'){
			isAdd.value = true
			formTitle.value = '新增' + formName
			formVisible.value = true
		}else if(formType == 'info'){
			isAdd.value = false
			formTitle.value = '查看' + formName
			getInfo()
		}else if(formType == 'edit'){
			isAdd.value = true
			formTitle.value = '修改' + formName
			getInfo()
		}
		else if(formType == 'cross'){
			isAdd.value = true
			formTitle.value = formNames
			// getInfo()
			for(let x in row){
				if(x=='shebeibianhao'){
					form.value.shebeibianhao = row[x];
					disabledForm.value.shebeibianhao = true;
					continue;
				}
				if(x=='shebeimingcheng'){
					form.value.shebeimingcheng = row[x];
					disabledForm.value.shebeimingcheng = true;
					continue;
				}
				if(x=='shebeixinghao'){
					form.value.shebeixinghao = row[x];
					disabledForm.value.shebeixinghao = true;
					continue;
				}
				if(x=='shebeishuliang'){
					form.value.shebeishuliang = row[x];
					disabledForm.value.shebeishuliang = true;
					continue;
				}
				if(x=='shebeiweizhi'){
					form.value.shebeiweizhi = row[x];
					disabledForm.value.shebeiweizhi = true;
					continue;
				}
				if(x=='shebeileixing'){
					form.value.shebeileixing = row[x];
					disabledForm.value.shebeileixing = true;
					continue;
				}
				if(x=='baoxiuneirong'){
					form.value.baoxiuneirong = row[x];
					disabledForm.value.baoxiuneirong = true;
					continue;
				}
				if(x=='baoxiushijian'){
					form.value.baoxiushijian = row[x];
					disabledForm.value.baoxiushijian = true;
					continue;
				}
				if(x=='renyuanzhanghao'){
					form.value.renyuanzhanghao = row[x];
					disabledForm.value.renyuanzhanghao = true;
					continue;
				}
				if(x=='renyuanxingming'){
					form.value.renyuanxingming = row[x];
					disabledForm.value.renyuanxingming = true;
					continue;
				}
				if(x=='weixiuyuanzhanghao'){
					form.value.weixiuyuanzhanghao = row[x];
					disabledForm.value.weixiuyuanzhanghao = true;
					continue;
				}
				if(x=='weixiuyuanxingming'){
					form.value.weixiuyuanxingming = row[x];
					disabledForm.value.weixiuyuanxingming = true;
					continue;
				}
			}
			if(row){
				crossRow.value = row
			}
			if(table){
				crossTable.value = table
			}
			if(tips){
				crossTips.value = tips
			}
			if(statusColumnName){
				crossColumnName.value = statusColumnName
			}
			if(statusColumnValue){
				crossColumnValue.value = statusColumnValue
			}
			formVisible.value = true
		}

		context?.$http({
			url: `${context?.$toolUtil.storageGet('sessionTable')}/session`,
			method: 'get'
		}).then(res => {
			var json = res.data.data
			if(json.hasOwnProperty('renyuanzhanghao')&& context?.$toolUtil.storageGet("role")!="管理员"){
				form.value.renyuanzhanghao = json.renyuanzhanghao
				disabledForm.value.renyuanzhanghao = true;
			}
			if(json.hasOwnProperty('renyuanxingming')&& context?.$toolUtil.storageGet("role")!="管理员"){
				form.value.renyuanxingming = json.renyuanxingming
				disabledForm.value.renyuanxingming = true;
			}
		})
		context?.$http({
			url: `option/shebeixinxi/shebeibianhao`,
			method: 'get'
		}).then(res=>{
			shebeibianhaoLists.value = res.data.data
		})
		//由上级字段带出不可改
		disabledForm.value.shebeimingcheng = true;
		//由上级字段带出不可改
		disabledForm.value.shebeixinghao = true;
		//由上级字段带出不可改
		disabledForm.value.shebeiweizhi = true;
		//由上级字段带出不可改
		disabledForm.value.shebeileixing = true;
		context?.$http({
			url: `option/weixiuyuan/weixiuyuanzhanghao`,
			method: 'get'
		}).then(res=>{
			weixiuyuanzhanghaoLists.value = res.data.data
		})
		//由上级字段带出不可改
		disabledForm.value.weixiuyuanxingming = true;
	}
	//初始化
	//声明父级调用
	defineExpose({
		init
	})
	//关闭
	const closeClick = () => {
		formVisible.value = false
	}
	const shebeibianhaoChange=()=>{
		context?.$http({
			url: `follow/shebeixinxi/shebeibianhao?columnValue=` + form.value.shebeibianhao,
			method: 'get'
		}).then(res=>{
			if(res.data.data.shebeimingcheng){
				form.value.shebeimingcheng = res.data.data.shebeimingcheng
			}
			if(res.data.data.shebeixinghao){
				form.value.shebeixinghao = res.data.data.shebeixinghao
			}
			if(res.data.data.shebeiweizhi){
				form.value.shebeiweizhi = res.data.data.shebeiweizhi
			}
			if(res.data.data.shebeileixing){
				form.value.shebeileixing = res.data.data.shebeileixing
			}
			if(res.data.data.weixiuyuanxingming){
				form.value.weixiuyuanxingming = res.data.data.weixiuyuanxingming
			}
		})
	}
	const weixiuyuanzhanghaoChange=()=>{
		context?.$http({
			url: `follow/weixiuyuan/weixiuyuanzhanghao?columnValue=` + form.value.weixiuyuanzhanghao,
			method: 'get'
		}).then(res=>{
			if(res.data.data.shebeimingcheng){
				form.value.shebeimingcheng = res.data.data.shebeimingcheng
			}
			if(res.data.data.shebeixinghao){
				form.value.shebeixinghao = res.data.data.shebeixinghao
			}
			if(res.data.data.shebeiweizhi){
				form.value.shebeiweizhi = res.data.data.shebeiweizhi
			}
			if(res.data.data.shebeileixing){
				form.value.shebeileixing = res.data.data.shebeileixing
			}
			if(res.data.data.weixiuyuanxingming){
				form.value.weixiuyuanxingming = res.data.data.weixiuyuanxingming
			}
		})
	}
	//富文本
	const editorChange = (e,name) =>{
		form.value[name] = e
	}
	//提交
	const save=()=>{
		var table = crossTable.value
		var objcross = JSON.parse(JSON.stringify(crossRow.value))
		let crossUserId = ''
		let crossRefId = ''
		let crossOptNum = ''
		if(type.value == 'cross'){
			if(crossColumnName.value!=''){
				if(!crossColumnName.value.startsWith('[')){
					for(let o in objcross){
						if(o == crossColumnName.value){
							objcross[o] = crossColumnValue.value
						}
					}
					//修改跨表数据
					changeCrossData(objcross)
				}else{
					crossUserId = context?.$toolUtil.storageGet('userid')
					crossRefId = objcross['id']
					crossOptNum = crossColumnName.value.replace(/\[/,"").replace(/\]/,"")
				}
			}
		}
		formRef.value.validate((valid)=>{
			if(valid){
				if(crossUserId&&crossRefId){
					form.value.crossuserid = crossUserId
					form.value.crossrefid = crossRefId
					let params = {
						page: 1,
						limit: 1000, 
						crossuserid:form.value.crossuserid,
						crossrefid:form.value.crossrefid,
					}
					context?.$http({
						url: `${tableName}/page`,
						method: 'get', 
						params: params 
					}).then(res=>{
						if(res.data.data.total>=crossOptNum){
							context?.$toolUtil.message(`${crossTips.value}`,'error')
							return false
						}else{
							context?.$http({
								url: `${tableName}/${!form.value.id ? "save" : "update"}`,
								method: 'post', 
								data: form.value 
							}).then(res=>{
								context?.$toolUtil.message(`操作成功`,'success',()=>{
									formVisible.value = false
									emit('formModelChange')
								})
							})
						}
					})
				}else{
					context?.$http({
						url: `${tableName}/${!form.value.id ? "save" : "update"}`,
						method: 'post', 
						data: form.value 
					}).then(res=>{
						context?.$toolUtil.message(`操作成功`,'success',()=>{
							formVisible.value = false
							emit('formModelChange')
						})
					})
				}
			}
		})
	}
	//修改跨表数据
	const changeCrossData=(row)=>{
		context?.$http({
			url: `${crossTable.value}/update`,
			method: 'post',
			data: row
		}).then(res=>{})
	}
</script>
<style lang="scss" scoped>
	// 表单
	.formModel_form{
		border-radius: 6px;
		padding: 30px;
		box-shadow: 0 0px 0px rgba(85, 255, 255, 0.5);
		// form item
		:deep(.el-form-item) {
			border: 1px solid #ddd;
			margin: 0;
			display: flex;
			//label
			.el-form-item__label {
			 border: 1px solid #ddd;
			 padding: 0;
			 background: rgba(236,236,236,1);
			 display: block;
			 width: 120px;
			 border-width: 0 2px 0 0;
			 text-align: center;
			}
			// 内容盒子
			.el-form-item__content {
				padding: 0 20px;
				display: flex;
				width: calc(100% - 120px);
				justify-content: flex-start;
				align-items: center;
				flex-wrap: wrap;
				// 输入框
				.list_inp {
					border: 1px solid #ddd;
					box-shadow: 0 0 0px rgba(85, 255, 255, 0.5);
					padding: 0 10px;
					width: 100%;
					line-height: 36px;
					box-sizing: border-box;
					height: 36px;
					//去掉默认样式
					.el-input__wrapper{
						border: none;
						box-shadow: none;
						background: none;
						border-radius: 0;
						height: 100%;
						padding: 0;
					}
					.is-focus {
						box-shadow: none !important;
					}
				}
				//日期选择器
				.list_date {
					border: 1px solid #ddd;
					border-radius: 0;
					box-shadow: 0 0 0px rgba(85, 255, 255, 0.5);
					width: 100%;
					line-height: 36px;
					box-sizing: border-box;
					//去掉默认样式
					.el-input__wrapper{
						border: none;
						box-shadow: none;
						background: none;
						border-radius: 0;
						height: 100%;
					}
				}
				// 下拉框
				.list_sel {
