<template>
	<div>
		<el-dialog v-model="formVisible" :title="formTitle" width="80%" destroy-on-close :fullscreen='false'>
			<el-form class="formModel_form" ref="formRef" :model="form" label-width="$template2.back.add.form.base.labelWidth" :rules="rules">
				<el-row>
					<el-col :span="12">
						<el-form-item label="设备编号" prop="shebeibianhao">
							<el-input class="list_inp" v-model="form.shebeibianhao" placeholder="设备编号"
								 type="text" 								:readonly="!isAdd||disabledForm.shebeibianhao?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="12">
						<el-form-item label="设备名称" prop="shebeimingcheng">
							<el-input class="list_inp" v-model="form.shebeimingcheng" placeholder="设备名称"
								 type="text" 								:readonly="!isAdd||disabledForm.shebeimingcheng?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="12">
						<el-form-item label="设备型号" prop="shebeixinghao">
							<el-input class="list_inp" v-model="form.shebeixinghao" placeholder="设备型号"
								 type="text" 								:readonly="!isAdd||disabledForm.shebeixinghao?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="12">
						<el-form-item label="设备位置" prop="shebeiweizhi">
							<el-input class="list_inp" v-model="form.shebeiweizhi" placeholder="设备位置"
								 type="text" 								:readonly="!isAdd||disabledForm.shebeiweizhi?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="12">
						<el-form-item label="出库数量" prop="shebeishuliang">
							<el-input class="list_inp" v-model.number="form.shebeishuliang" placeholder="出库数量"
								 type="text" 								:readonly="!isAdd||disabledForm.shebeishuliang?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="12">
						<el-form-item label="出库时间" prop="chukushijian">
							<el-date-picker
								class="list_date"
								v-model="form.chukushijian"
								format="YYYY-MM-DD HH:mm:ss"
								value-format="YYYY-MM-DD HH:mm:ss"
								type="datetime"
								:readonly="!isAdd||disabledForm.chukushijian?true:false"
								placeholder="请选择出库时间" />
						</el-form-item>
					</el-col>
					<el-col :span="12">
						<el-form-item label="仓管账号" prop="cangguanzhanghao">
							<el-input class="list_inp" v-model="form.cangguanzhanghao" placeholder="仓管账号"
								 type="text" 								:readonly="!isAdd||disabledForm.cangguanzhanghao?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="12">
						<el-form-item label="仓管姓名" prop="cangguanxingming">
							<el-input class="list_inp" v-model="form.cangguanxingming" placeholder="仓管姓名"
								 type="text" 								:readonly="!isAdd||disabledForm.cangguanxingming?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="12">
						<el-form-item label="注意事项" prop="zhuyishixiang">
							<el-input v-model="form.zhuyishixiang" placeholder="注意事项" type="textarea"
							:readonly="!isAdd||disabledForm.zhuyishixiang?true:false"
							/>
						</el-form-item>
					</el-col>
				</el-row>
			</el-form>
			<template #footer v-if="isAdd||type=='logistics'||type=='reply'">
				<span class="formModel_btn_box">
					<el-button class="formModel_cancel" @click="closeClick">取消</el-button>
					<el-button class="formModel_confirm" type="primary" @click="save"
						>
						提交
					</el-button>
				</span>
			</template>
		</el-dialog>
	</div>
</template>
<script setup>
	import {
		reactive,
		ref,
		getCurrentInstance,
		nextTick,
		computed,
		defineEmits
	} from 'vue'
	const context = getCurrentInstance()?.appContext.config.globalProperties;	
	const emit = defineEmits(['formModelChange'])
	//基础信息
	const tableName = 'shebeichuku'
	const formName = '设备出库'
	//基础信息
	//form表单
	const form = ref({})
	const disabledForm = ref({
		shebeibianhao : false,
		shebeimingcheng : false,
		shebeixinghao : false,
		shebeiweizhi : false,
		zhuyishixiang : false,
		shebeishuliang : false,
		chukushijian : false,
		cangguanzhanghao : false,
		cangguanxingming : false,
	})
	const formVisible = ref(false)
	const isAdd = ref(false)
	const formTitle = ref('')
	//表单验证
	//匹配整数
	const validateIntNumber = (rule, value, callback) => {
		if (!value) {
			callback();
		} else if (!context?.$toolUtil.isIntNumer(value)) {
			callback(new Error("请输入整数"));
		} else {
			callback();
		}
	}
	//匹配数字
	const validateNumber = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isNumber(value)) {
			callback(new Error("请输入数字"));
		} else {
			callback();
		}
	}
	//匹配手机号码
	const validateMobile = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isMobile(value)) {
			callback(new Error("请输入正确的手机号码"));
		} else {
			callback();
		}
	}
	//匹配电话号码
	const validatePhone = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isPhone(value)) {
			callback(new Error("请输入正确的电话号码"));
		} else {
			callback();
		}
	}
	//匹配邮箱
	const validateEmail = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isEmail(value)) {
			callback(new Error("请输入正确的邮箱地址"));
		} else {
			callback();
		}
	}
	//匹配身份证
	const validateIdCard = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.checkIdCard(value)) {
			callback(new Error("请输入正确的身份证号码"));
		} else {
			callback();
		}
	}
	//匹配网站地址
	const validateUrl = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isURL(value)) {
			callback(new Error("请输入正确的URL地址"));
		} else {
			callback();
		}
	}
	const rules = ref({
		shebeibianhao: [
		],
		shebeimingcheng: [
			{required: true,message: '请输入',trigger: 'blur'}, 
		],
		shebeixinghao: [
			{required: true,message: '请输入',trigger: 'blur'}, 
		],
		shebeiweizhi: [
		],
		zhuyishixiang: [
		],
		shebeishuliang: [
			{ validator: validateIntNumber, trigger: 'blur' },
		],
		chukushijian: [
		],
		cangguanzhanghao: [
		],
		cangguanxingming: [
		],
	})
	//表单验证
	
	const formRef = ref(null)
	const id = ref(0)
	const type = ref('')
	//methods

	//获取唯一标识
	const getUUID =()=> {
      return new Date().getTime();
    }
	//重置
	const resetForm = () => {
		form.value = {
			shebeibianhao: '',
			shebeimingcheng: '',
			shebeixinghao: '',
			shebeiweizhi: '',
			zhuyishixiang: '',
			shebeishuliang: '',
			chukushijian: '',
			cangguanzhanghao: '',
			cangguanxingming: '',
		}
	}
	//获取info
	const getInfo = ()=>{
		context?.$http({
			url: `${tableName}/info/${id.value}`,
			method: 'get'
		}).then(res => {
			let reg=new RegExp('../../../file','g')
			form.value = res.data.data
			formVisible.value = true
		})
	}
	const crossRow = ref('')
	const crossTable = ref('')
	const crossTips = ref('')
	const crossColumnName = ref('')
	const crossColumnValue = ref('')
	//初始化
	const init=(formId=null,formType='add',formNames='',row=null,table=null,statusColumnName=null,tips=null,statusColumnValue=null)=>{
		resetForm()
			form.value.chukushijian = context?.$toolUtil.getCurDateTime()
		if(formId){
			id.value = formId
			type.value = formType
		}
		if(formType == 'add'){
			isAdd.value = true
			formTitle.value = '新增' + formName
			formVisible.value = true
		}else if(formType == 'info'){
			isAdd.value = false
			formTitle.value = '查看' + formName
			getInfo()
		}else if(formType == 'edit'){
			isAdd.value = true
			formTitle.value = '修改' + formName
			getInfo()
		}
		else if(formType == 'cross'){
			isAdd.value = true
			formTitle.value = formNames
			// getInfo()
			for(let x in row){
				if(x=='shebeibianhao'){
					form.value.shebeibianhao = row[x];
					disabledForm.value.shebeibianhao = true;
					continue;
				}
				if(x=='shebeimingcheng'){
					form.value.shebeimingcheng = row[x];
					disabledForm.value.shebeimingcheng = true;
					continue;
				}
				if(x=='shebeixinghao'){
					form.value.shebeixinghao = row[x];
					disabledForm.value.shebeixinghao = true;
					continue;
				}
				if(x=='shebeiweizhi'){
					form.value.shebeiweizhi = row[x];
					disabledForm.value.shebeiweizhi = true;
					continue;
				}
				if(x=='zhuyishixiang'){
					form.value.zhuyishixiang = row[x];
					disabledForm.value.zhuyishixiang = true;
					continue;
				}
				if(x=='shebeishuliang'){
					form.value.shebeishuliang = row[x];
					disabledForm.value.shebeishuliang = true;
					continue;
				}
				if(x=='chukushijian'){
					form.value.chukushijian = row[x];
					disabledForm.value.chukushijian = true;
					continue;
				}
				if(x=='cangguanzhanghao'){
					form.value.cangguanzhanghao = row[x];
					disabledForm.value.cangguanzhanghao = true;
					continue;
				}
				if(x=='cangguanxingming'){
					form.value.cangguanxingming = row[x];
					disabledForm.value.cangguanxingming = true;
					continue;
				}
			}
			if(row){
				crossRow.value = row
			}
			if(table){
				crossTable.value = table
			}
			if(tips){
				crossTips.value = tips
			}
			if(statusColumnName){
				crossColumnName.value = statusColumnName
			}
			if(statusColumnValue){
				crossColumnValue.value = statusColumnValue
			}
			form.value.shebeishuliang = 0
			disabledForm.value.shebeishuliang = false;
			formVisible.value = true
		}

		context?.$http({
			url: `${context?.$toolUtil.storageGet('sessionTable')}/session`,
			method: 'get'
		}).then(res => {
			var json = res.data.data
			if(json.hasOwnProperty('cangguanzhanghao')&& context?.$toolUtil.storageGet("role")!="管理员"){
				form.value.cangguanzhanghao = json.cangguanzhanghao
				disabledForm.value.cangguanzhanghao = true;
			}
			if(json.hasOwnProperty('cangguanxingming')&& context?.$toolUtil.storageGet("role")!="管理员"){
				form.value.cangguanxingming = json.cangguanxingming
				disabledForm.value.cangguanxingming = true;
			}
		})
	}
	//初始化
	//声明父级调用
	defineExpose({
		init
	})
	//关闭
	const closeClick = () => {
		formVisible.value = false
	}
	//富文本
	const editorChange = (e,name) =>{
		form.value[name] = e
	}
	//提交
	const save=()=>{
		var table = crossTable.value
		var objcross = JSON.parse(JSON.stringify(crossRow.value))
		if(objcross!='') {
			objcross.shebeishuliang = objcross.shebeishuliang - form.value.shebeishuliang
			if(objcross.shebeishuliang<0){
				context?.$toolUtil.message('出库数量不足','error')
				return false
			}
		}
		let crossUserId = ''
		let crossRefId = ''
		let crossOptNum = ''
		if(type.value == 'cross'){
			if(crossColumnName.value!=''){
				if(!crossColumnName.value.startsWith('[')){
					for(let o in objcross){
						if(o == crossColumnName.value){
							objcross[o] = crossColumnValue.value
						}
					}
					//修改跨表数据
					changeCrossData(objcross)
				}else{
					crossUserId = context?.$toolUtil.storageGet('userid')
					crossRefId = objcross['id']
					crossOptNum = crossColumnName.value.replace(/\[/,"").replace(/\]/,"")
				}
			}
		}
		formRef.value.validate((valid)=>{
			if(valid){
				if(crossUserId&&crossRefId){
					form.value.crossuserid = crossUserId
					form.value.crossrefid = crossRefId
					let params = {
						page: 1,
						limit: 1000, 
						crossuserid:form.value.crossuserid,
						crossrefid:form.value.crossrefid,
					}
					context?.$http({
						url: `${tableName}/page`,
						method: 'get', 
						params: params 
					}).then(res=>{
						if(res.data.data.total>=crossOptNum){
							context?.$toolUtil.message(`${crossTips.value}`,'error')
							return false
						}else{
							context?.$http({
								url: `${tableName}/${!form.value.id ? "save" : "update"}`,
								method: 'post', 
								data: form.value 
							}).then(res=>{
									//修改跨表数据
									changeCrossData(objcross)
								context?.$toolUtil.message(`操作成功`,'success',()=>{
									formVisible.value = false
									emit('formModelChange')
								})
							})
						}
					})
				}else{
					context?.$http({
						url: `${tableName}/${!form.value.id ? "save" : "update"}`,
						method: 'post', 
						data: form.value 
					}).then(res=>{
							//修改跨表数据
							changeCrossData(objcross)
						context?.$toolUtil.message(`操作成功`,'success',()=>{
							formVisible.value = false
							emit('formModelChange')
						})
					})
				}
			}
		})
	}
	//修改跨表数据
	const changeCrossData=(row)=>{
		context?.$http({
			url: `${crossTable.value}/update`,
			method: 'post',
			data: row
		}).then(res=>{})
	}
</script>
<style lang="scss" scoped>
	// 表单
	.formModel_form{
		border-radius: 6px;
		padding: 30px;
		box-shadow: 0 0px 0px rgba(85, 255, 255, 0.5);
		// form item
		:deep(.el-form-item) {
			border: 1px solid #ddd;
			margin: 0;
			display: flex;
			//label
			.el-form-item__label {
			 border: 1px solid #ddd;
			 padding: 0;
			 background: rgba(236,236,236,1);
			 display: block;
			 width: 120px;
			 border-width: 0 2px 0 0;
			 text-align: center;
			}
			// 内容盒子
			.el-form-item__content {
				padding: 0 20px;
				display: flex;
				width: calc(100% - 120px);
				justify-content: flex-start;
				align-items: center;
				flex-wrap: wrap;
				// 输入框
				.list_inp {
					border: 1px solid #ddd;
					box-shadow: 0 0 0px rgba(85, 255, 255, 0.5);
					padding: 0 10px;
					width: 100%;
					line-height: 36px;
					box-sizing: border-box;
					height: 36px;
					//去掉默认样式
					.el-input__wrapper{
						border: none;
						box-shadow: none;
						background: none;
						border-radius: 0;
						height: 100%;
						padding: 0;
					}
					.is-focus {
						box-shadow: none !important;
					}
				}
				//日期选择器
				.list_date {
					border: 1px solid #ddd;
					border-radius: 0;
					box-shadow: 0 0 0px rgba(85, 255, 255, 0.5);
					width: 100%;
					line-height: 36px;
					box-sizing: border-box;
					//去掉默认样式
					.el-input__wrapper{
						border: none;
						box-shadow: none;
						background: none;
						border-radius: 0;
						height: 100%;
					}
				}
				// 长文本
				.el-textarea__inner {
					border: 1px solid #ddd;
					border-radius: 0;
					padding: 12px;
					outline: none;
					color: #333;
					width: 100%;
					font-size: 14px;
					min-height: 120px;
				}
			}
		}
	}
	// 按钮盒子
	.formModel_btn_box {
		padding: 40px 0 0;
		display: flex;
		width: 100%;
		justify-content: center;
		align-items: center;
		.formModel_cancel {
			border: 0;
			cursor: pointer;
			border-radius: 4px;
			padding: 0 24px;
			margin: 0 10px 0 0;
			outline: none;
			color: #fff;
			background: rgba(27, 78, 133, 1);
			width: auto;
			font-size: 14px;
			height: 32px;
		}
		.formModel_cancel:hover {
			background: rgba(27, 78, 133, 0.5);
		}
		
		.formModel_confirm {
			border: 0;
			cursor: pointer;
			border-radius: 4px;
			padding: 0 24px;
			margin: 0 10px 0 0;
			outline: none;
			color: #fff;
			background: rgba(241, 49, 100, 1);
			width: auto;
			font-size: 14px;
			height: 32px;
		}
		.formModel_confirm:hover {
			background: rgba(241, 49, 100, .5);
		}
	}
</style>